import React from 'react';
import { NavLink } from 'react-router-dom';


const Card = (props) => {
    let url="";
    return ( 
        <React.Fragment>
            <div className="col-md-4 col-10 mx-auto my-3">
                <div className="card">
                    <img src={props.imgsrc} className="card-img-top" alt={props.title} />
                    <div className="card-body">
                        <h5 className="card-title-bold font-weight">{props.title}</h5>
                        <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                        <NavLink to={url} className="btn btn-primary">Go somewhere</NavLink>
                    </div>
                </div>
            </div>
        </React.Fragment>
     );
}
 
export default Card;