import React,{useState} from 'react';

const Contact = () => {
    const [data, setData]=useState({
        fullname:'',
        phone :'',
        email:'',
        msg:'',
    });

    const inputSubmit=(event)=>{
        const {name, value}=event.target;
        setData((prevVal)=>{
            return{
                ...prevVal,
                [name]:value,
            }
        })
    }
    const formSubmit=(e)=>{
        e.preventDefault();
        alert(`${data.fullname}`)
    }
    return ( 
        <React.Fragment>
            <div className="my-5">
                <h1 className="heading">Contact Us</h1>
            </div>
            <div className="container contact_div">
                <div className="row">
                    <div className="col-md-6 col-10 mx-auto">
                        <form onSubmit={formSubmit}>
                            <div className="form-group">
                                <label htmlFor="exampleFormControlInput1">Full Name</label>
                                <input type="text" className="form-control" id="exampleFormControlInput1" name="fullname" value={data.fullname} onChange={inputSubmit} placeholder="Enter Your Name" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="exampleFormControlInput1">Mobile no.</label>
                                <input type="number" className="form-control" id="exampleFormControlInput1" name="phone" value={data.phone} onChange={inputSubmit} placeholder="mobile number" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="exampleFormControlInput1">Email address</label>
                                <input type="email" className="form-control" id="exampleFormControlInput1" name="email" value={data.email} onChange={inputSubmit} placeholder="name@example.com" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="exampleFormControlTextarea1">Message</label>
                                <textarea className="form-control" id="exampleFormControlTextarea1" rows="3" name="msg" value={data.msg} onChange={inputSubmit}></textarea>
                            </div>
                            <button className="btn btn-primary" type="submit">Submit form</button>
                        </form>
                    </div>
                </div>
            </div>
        </React.Fragment>
     );
}
 
export default Contact;